    description:
      "<p>You will be joining a platform focused on building software products and services that are critical to the Research and Development pipeline. These include core systems that track the genes and associated meta data that have the potential to be the next blockbuster biotech trait for insect and weed resistance, increased yield or improved quality.</p>\n<p>You may also be involved in building products that support the Agricultural biologicals business. You will have an opportunity to drive innovation in agriculture via software products that support cutting edge technologies including Next Gen Sequencing and Gene editing.</p>\n<p>This team specifically works on the following products: Sequence Utility Resource (SeqUR) is a sequence repository and data asset housing pipeline constructs, genes, proteins, and collaboration sequences allowing users to store, search, and analyze sequence data that helps fuel Crop Science R&amp;D pipeline.</p>\n<p>Our products include real-time streaming analytics from an expanding collection of connected devices, mobile data collection, visualization suites, big data genomics repositories, and geospatial computing systems, just to name a few. To build great software, we need great people.</p>\n<p>Skills Required (Bare Minimum): NodeJS, AWS Lambda, Cloudformation Templates, Vue, React, SQL, Postgres, strong communication skills, good teaming and collaboration skills, passion for learning.</p>\n<p>Job Description:\n-Work as part of an agile development team to build and support applications.\n-Work with team and product manager to manage project priorities, deadlines, and deliverables.\n-Interact directly with business customers to gather and understand requirements and how they translate in application features.\n-Develop applications and services using the right tool for the job. -Conduct functional and non-functional testing.\n-Troubleshoot and debug applications.\n-Deploy applications across all environments in collaboration with development team, product management, and delivery.\n-Focus on code quality, including performing unit and integration testing.\n-Evaluate existing applications to refactor, update and add new features.\n-Develop any technical documentation needed to accurately represent application design and code.\n-Help to shape the vision for their own team's products, taking in to account impacts, dependencies, and opportunities to collaborate with other teams and groups.\n-Identify and pursue opportunities to improve own knowledge and skills.\n-Identify opportunities to develop new and better solutions.\n-Implement solutions to technical obstacles affecting own team</p>\n<p>Learn &amp; Grow:\nOngoing focus on growth through training (domain and technical) and knowledge sharing through communities of agile teams.\nInnovation days, hack-a-thons, networking events, and volunteer opportunities.\nDiverse technology stacks in a forward thinking, open source-centric environment.</p>\n<p>Collaborate:\nWork with other talented people solving a wide range of complex and unique challenges that have real world impact.\nOpen workspace promotes collaboration and pairing in a fun, casual environment.\nWe value your talent over a role or title. Strength of ideas valued over position on an org chart.</p>\n<p>Culture:\nWork life balance and flexibility is not just discussed, it is our way of life.\nChallenging how things are done is not just encouraged, it is expected.\nWe value autonomous, self-organizing teams allowing you to have a real say in what you work on, who you work with, and how you work.</p>\n<p>Ownership:\nInteract directly with customers to create critical business solutions. Be responsible for architecting, implementing, testing, deploying, and supporting the software that drives Biotech Company Crop Science.\nWork with your team and product owner to manage project priorities, deadlines and deliverables.</p>\n",
